import React, { Fragment } from 'react';
import styles from './paragraph.css';
import classnames from 'classnames';

type Props = {
  children: string;
  margin?: boolean;
  className?: string;
};

export default function Paragraph({ children = '', margin }: Props) {
  return (
    <p
      className={classnames(
        styles.paragraph,
        margin && styles.margin,
        className,
      )}
    >
      {children.split('\n').map((item, key) => (
        <Fragment key={key}>
          {item}
          <br />
        </Fragment>
      ))}
    </p>
  );
}
